.navbar {
    width: 100%;
    height: 49px;
    background-color: #FFFFFF;
    border-bottom: 1px solid rgba(0, 0, 0, 0.72);
}

.navbarWrapper {
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logoImage {
    width: 40px;
    height: 40px;
}

.topRight {
    display: flex;
    align-items: center;
}

.navbarTab {
    margin: 0px 50px;
}

.navbarTab > a {
    text-decoration: none;
    color: #000000;
}

.active {
    font-weight: 700;
}

.rectangle {
    height: 30px;
    width: 4px;
    background: #F3F3F3;
    border-radius: 5000px;
}

.profileDiv {
    display: flex;
    align-items: center;
    
}

.profileImage {
    width: 33px;
    height: 33px;
    border-radius: 50%;
    margin: 0px 10px 0px 20px;
}

.courseName {
    margin: 0;
    font-size: 10px;
    font-weight: 400;
    color: #939393;
    
}

.educatorName {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    color: #000000;
}


